body {
  margin: 0 auto;

  @include Roboto-style-common(20px, normal, 22px, #111111, left, none);
}

.visually-hidden:not(:focus):not(:active),
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main {
  overflow: hidden;
}
